import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';

import fetchPlanets from '../helpers/fetchFunctions';
import PlanetsContext from './planetsContext';

const alternatives = [
  'population',
  'orbital_period',
  'diameter',
  'rotation_period',
  'surface_water',
];

function PlanetsProvider({ children }) {
  // useState permite que utilize o estado do React
  const [planets, setPlanets] = useState([]);
  const [allPlanets, setAllPlanets] = useState([]);
  const [filterName, setfilterName] = useState({
    input: '',
  });
  const [column, setColumn] = useState('population');
  const [comparison, setComparison] = useState('maior que');
  const [amount, setAmount] = useState('0');
  const [options, setOptions] = useState(alternatives);
  const [filters, setFilters] = useState([]);
  const [sortColumn, setSortColumn] = useState('population');
  const [sortRadio, setSortRadio] = useState('');
  const [order, setOrder] = useState({ order: { column: '', sort: '' } });

  // Atualiza estado para preencher a tabela
  const getPlanets = async () => {
    const data = await fetchPlanets('https://swapi.dev/api/planets');
    setPlanets(data.results);
    setAllPlanets(data.results);
  };

  // Essa callback ser executada similarmente ao componentDidMount, ou seja, roda apenas uma vez
  useEffect(() => {
    getPlanets();
  }, []);

  // handleChange est atualizando o valor do estado de maneira que seja o mesmo que est sendo digitado no input
  const handleChange = ({ target: { value } }) => {
    setfilterName({
      input: value,
    });
  };

  const handleSelectedFilter = () => {
    // Esse cdigo  usado para filtrar um array de opes com base na coluna selecionada pelo usurio.
    // O objetivo  remover a opo atual da lista de opes disponveis.
    const newOptions = options.filter((option) => option !== column);
    setColumn(newOptions[0]);
    setOptions(newOptions);

    // Esse cdigo  usado para incrementar um array de objetos com as opes de filtro selecionadas pelo usurio.
    // Seu objetivo  armazenar todos os filtros utilizados durante a navegao.
    setFilters([...filters, { column, comparison, amount }]);

    // O objetivo do cdigo abaixo  filtrar os resultados da tabela
    // Ela utiliza a varivel "comparison" para determinar qual tipo de filtro deve ser aplicado
    const filteredPlanets = planets.filter((planet) => {
      switch (comparison) {
      case 'maior que':
        return parseInt(planet[column], 10) > parseInt(amount, 10);
      case 'menor que':
        return parseInt(planet[column], 10) < parseInt(amount, 10);
      case 'igual a':
        return parseInt(planet[column], 10) === parseInt(amount, 10);
      default:
        return false;
      }
    });
    setPlanets(filteredPlanets);
  };

  // O objetivo da funo abaixo  remover todos os filtros usados na tabela
  // E retornar a tabela e os filtros para sua condio inicial
  const removeAllFilters = async () => {
    await getPlanets();
    setColumn('population');
    setAmount('0');
    setOptions(alternatives);
    setFilters([]);
  };

  const removeSelectedFilter = (selectedFilter) => {
    // A seguir, o objetivo desse trecho  obter a posio exata de uma opo na seleo original
    const index = alternatives.indexOf(selectedFilter);
    // A seguir, um filtro excludo retorna para as opes de escolha
    options.splice(index, 0, selectedFilter);

    // Esse cdigo  usado para remover os filtros excludos do array com as opes de filtro selecionadas pelo usurio.
    // Seu objetivo  atualizar os filtros novamente aps a excluso de um.
    const newFilters = filters.filter((element) => element.column !== selectedFilter);
    setFilters(newFilters);

    if (newFilters.length === 0) { // Se no houver mais filtros selecionados, a tabela retorna para sua condio inicial
      setColumn('population');
      setAmount('0');
      setOptions(alternatives);
      setPlanets(allPlanets);
    } else {
      newFilters.forEach((element) => { // Se houver filtros selecionados restantes, esse array  percorrido gerando uma nova tabela com base nos filtros restantes
        if (element.comparison === 'maior que') {
          const newData = allPlanets.filter(
            (e) => +e[element.column] > +element.amount,
          );
          setOptions(options.filter((e) => e !== element.column));
          setColumn(options[0]);
          setAmount('0');
          setPlanets(newData);
        }

        if (element.comparisonFilter === 'igual a') {
          const newData = allPlanets.filter(
            (e) => +e[element.column] === +element.amount,
          );

          setColumn(column.filter((e) => e !== element.column));
          setColumn(options[0]);
          setAmount('0');
          setPlanets(newData);
        }

        if (element.comparison === 'menor que') {
          const newData = allPlanets.filter(
            (e) => +e[element.column] < +element.amount,
          );

          setOptions(column.filter((e) => e !== element.column));
          setColumn(options[0]);
          setAmount('0');
          setPlanets(newData);
        }
      });
    }
  };

  // Essa funo  usada para classificar os dados da tabela.
  // A funo recebe um objeto como parmetro que contm informaes sobre a coluna e a direo da classificao (ascendente ou descendente): obj = { order: { column: sortColumn, sort: sortRadio } }
  // O valor bellow  usado para garantir que os elementos unknown sejam sempre enviados para o final da tabela.
  const handleSorting = (obj) => {
    setOrder(obj);
    const below = -1;
    const dataSort = [...planets];
    if (obj.order.sort === 'DESC') {
      dataSort.sort((a, b) => (
        b[obj.order.column] === 'unknown'
          ? below
          : parseInt(b[obj.order.column], 10) - parseInt(a[obj.order.column], 10)
      ));
    } else if (obj.order.sort === 'ASC') {
      dataSort.sort((a, b) => (
        b[obj.order.column] === 'unknown'
          ? below
          : parseInt(a[obj.order.column], 10) - parseInt(b[obj.order.column], 10)
      ));
    }
    setPlanets(dataSort);
  };

  // Definindo os dados que sero compartilhados para os componentes
  const value = {
    planets,
    handleChange,
    filterName,
    column,
    comparison,
    amount,
    setColumn,
    setComparison,
    setAmount,
    handleSelectedFilter,
    options,
    filters,
    removeAllFilters,
    removeSelectedFilter,
    sortColumn,
    sortRadio,
    order,
    setSortColumn,
    setSortRadio,
    handleSorting,
  };

  // Com esse retorno todos os componentes encapsulados pelo PlanetsContext.Provider tero acesso a esses dados
  return (
    <PlanetsContext.Provider value={ value }>
      { children }
    </PlanetsContext.Provider>
  );
}

// Validao exigida pelo Eslint
PlanetsProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

export default PlanetsProvider;
